<template>
  <div id="editPreview">
    <header-component />
    <div id="loadingPreview" v-loading="loading">
      <div id="editpagePreview" v-show="onload">
        <div
          id="questionnairePreview"
          ref="previewHeight"
          @scroll="scrollDefault"
        >
          <div id="topBox" class="box">
            预览
          </div>
          <div id="queBoxPreview" class="box">
            <div id="headPreview">
              <div id="namePreview">
                <span>{{ queName }}</span>
              </div>
              <div id="interpretationPreview">
                <span>
                  {{ queIntp }}
                </span>
              </div>
            </div>
            <div id="modelsPreview" ref="models">
              <div
                v-for="que in quesFiltered"
                :key="que.id"
                class="list-complete-item"
              >
                <el-form
                  v-if="whetherShowUp(que.showUpCondition)"
                  style="margin:15px 20px;"
                  :model="que"
                  ref="que"
                >
                  <div class="que-title-holder">
                    <div class="redStarHolder">
                      <img
                        v-if="que.compulsory"
                        src="../assets/pictures/redStar.svg"
                        alt="*"
                        width="7px"
                        height="7px"
                      />
                      <div v-else style="width: 7px;height: 7px;"></div>
                    </div>
                    <span
                      >{{ queUltimateIndex(que.id) + 1 }}. {{ que.title }}
                      <div class="restriction-tip">
                        {{ restrictionTip(que) }}
                      </div></span
                    >
                  </div>
                  <div class="pic-preview">
                    <el-image
                      v-if="que.titleImgSrc != ''"
                      :src="que.titleImgSrc"
                      :preview-src-list="[que.titleImgSrc]"
                      fit="cover"
                      style="width: 135px;height: 90px;border-radius: 5px;"
                    ></el-image>
                  </div>

                  <el-form-item
                    v-if="que.type == '单选题'"
                    prop="radio.value"
                    :rules="{
                      validator: checkStr.bind(que),
                      trigger: 'change',
                    }"
                  >
                    <el-radio-group
                      v-for="(choice, index) in que.radio.choices"
                      :key="choice.id"
                      class="choices-item"
                      v-model="que.radio.value"
                    >
                      <li>
                        <div>
                          <el-radio
                            :label="
                              choice.blank_fill === ''
                                ? choice.content
                                : choice.content + '|ST23|' + choice.blank_fill
                            "
                            >{{ choice.content }}</el-radio
                          >
                        </div>
                      </li>
                      <el-form-item
                        v-if="
                          chosen(que.radio.value, choice.content) &&
                            choice.needFilling
                        "
                        :prop="'radio.choices.' + index + '.blank_fill'"
                        :rules="{
                          required: choice.compulsory,
                          message: '文本框内容必须填写',
                          trigger: 'blur',
                        }"
                      >
                        <div
                          class="bfa-input"
                          :style="choice.imgSrc ? 'margin-bottom: 5px;' : ''"
                        >
                          <div class="redStarHolder">
                            <img
                              v-if="choice.compulsory"
                              src="../assets/pictures/redStar.svg"
                              alt="*"
                              width="7px"
                              height="7px"
                            />
                            <div v-else style="width: 7px;height: 0px;"></div>
                          </div>
                          <el-input
                            v-model="choice.blank_fill"
                            @input="choiceRemainChosen(choice, que.radio)"
                            v-focus
                          ></el-input>
                        </div>
                      </el-form-item>
                      <div class="pic-preview">
                        <el-image
                          v-if="choice.imgSrc != ''"
                          :src="choice.imgSrc"
                          :preview-src-list="[choice.imgSrc]"
                          fit="cover"
                          style="width: 135px;height: 90px;border-radius: 5px;"
                        ></el-image>
                      </div>
                    </el-radio-group>
                  </el-form-item>

                  <el-form-item
                    v-else-if="que.type == '多选题'"
                    prop="multiple.value"
                    :rules="{
                      validator: checkArray.bind(que),
                      trigger: 'change',
                    }"
                  >
                    <el-checkbox-group
                      v-for="(option, index) in que.multiple.options"
                      :key="option.id"
                      class="options-item"
                      v-model="que.multiple.value"
                    >
                      <li>
                        <div>
                          <el-checkbox
                            :label="
                              option.blank_fill === ''
                                ? option.content
                                : option.content + '|ST23|' + option.blank_fill
                            "
                            >{{ option.content }}</el-checkbox
                          >
                        </div>
                      </li>
                      <el-form-item
                        v-if="
                          chosen(que.multiple.value, option.content) &&
                            option.needFilling
                        "
                        :prop="'multiple.options.' + index + '.blank_fill'"
                        :rules="{
                          required: option.compulsory,
                          message: '文本框内容必须填写',
                          trigger: 'blur',
                        }"
                      >
                        <div
                          class="bfa-input"
                          :style="option.imgSrc ? 'margin-bottom: 5px;' : ''"
                        >
                          <div class="redStarHolder">
                            <img
                              v-if="option.compulsory"
                              src="../assets/pictures/redStar.svg"
                              alt="*"
                              width="7px"
                              height="7px"
                            />
                            <div v-else style="width: 7px;height: 0px;"></div>
                          </div>
                          <el-input
                            v-model="option.blank_fill"
                            @input="optionRemainChosen(option, que.multiple)"
                            v-focus
                          ></el-input>
                        </div>
                      </el-form-item>
                      <div class="pic-preview">
                        <el-image
                          v-if="option.imgSrc != ''"
                          :src="option.imgSrc"
                          :preview-src-list="[option.imgSrc]"
                          fit="cover"
                          style="width: 135px;height: 90px;border-radius: 5px;"
                        ></el-image>
                      </div>
                    </el-checkbox-group>
                  </el-form-item>

                  <el-form-item
                    v-else-if="que.type == '填空题'"
                    prop="blank_fill"
                    :rules="{
                      validator: checkStr.bind(que),
                      trigger: 'blur',
                    }"
                  >
                    <div>
                      <el-input
                        type="textarea"
                        v-model="que.blank_fill"
                        class="blank"
                        autosize
                        placeholder="文本框"
                      ></el-input>
                    </div>
                  </el-form-item>
                  <el-form-item
                    v-else-if="que.type == '排序题'"
                    prop="sort.sortedNum"
                    :rules="{
                      validator: isCompleted.bind(que),
                      trigger: 'blur',
                    }"
                  >
                    <div
                      v-for="sortItem in que.sort.sortItems"
                      :key="sortItem.id"
                      class="sortItems-item"
                    >
                      <li>
                        <div>
                          <div
                            :class="[
                              'orderBox',
                              sortItem.order ? 'orderBox-active' : '',
                            ]"
                            @click="sortHandler(que.sort, sortItem)"
                          >
                            {{ sortItem.order ? sortItem.order : "" }}
                          </div>
                        </div>
                        <span>
                          {{ sortItem.content }}
                        </span>
                      </li>
                      <div class="pic-preview">
                        <el-image
                          v-if="sortItem.imgSrc != ''"
                          :src="sortItem.imgSrc"
                          :preview-src-list="[sortItem.imgSrc]"
                          fit="cover"
                          style="width: 135px;height: 90px;border-radius: 5px;"
                        ></el-image>
                      </div>
                    </div>
                  </el-form-item>
                  <el-form-item
                    v-else-if="que.type == '评分题'"
                    prop="slider.value"
                    :rules="[
                      {
                        validator: isZeroRemaining.bind(que),
                        trigger: 'change',
                      },
                    ]"
                  >
                    <div>
                      <div class="slider">
                        <el-slider
                          v-model="que.slider.value"
                          :marks="que.slider.marks"
                          :min="que.slider.min"
                          :max="que.slider.max"
                          :step="
                            que.slider.max - que.slider.min > 10 ? 0.1 : 0.01
                          "
                          @change="que.slider.showToolTip = true"
                          :show-tooltip="que.slider.showToolTip"
                        >
                        </el-slider>
                      </div>
                    </div>
                  </el-form-item>
                  <el-form-item
                    v-else-if="que.type == '矩阵单选题'"
                    prop="matrixRadio.rowVals"
                    :rules="[
                      {
                        validator: checkMatrixStr_change.bind(que),
                        trigger: 'change',
                      },
                      {
                        validator: checkMatrixStr_blur.bind(que),
                        trigger: 'blur',
                      },
                    ]"
                  >
                    <div class="matrix-holder">
                      <div class="header-preview">
                        <div class="empty"></div>
                        <div class="columnTitles">
                          <div
                            v-for="(column, index) in que.matrixRadio
                              .columnVals"
                            :key="index"
                          >
                            <span>{{ column.content }}</span>
                          </div>
                        </div>
                      </div>
                      <div
                        v-for="(row, index) in que.matrixRadio.rowVals"
                        :key="index"
                        class="row-item"
                      >
                        <div class="row-item-title">{{ row.content }}</div>
                        <div class="row-item-choices">
                          <el-radio-group
                            v-for="(column, index) in que.matrixRadio
                              .columnVals"
                            :key="index"
                            v-model="row.value"
                            class="row-item-choice"
                          >
                            <div>
                              <el-radio :label="column.content" class="justify">
                                <br />
                              </el-radio>
                            </div>
                          </el-radio-group>
                        </div>
                      </div>
                    </div>
                  </el-form-item>
                  <el-form-item
                    v-else
                    prop="matrixMultiple.rowVals"
                    :rules="[
                      {
                        validator: checkMatrixArray_change.bind(que),
                        trigger: 'change',
                      },
                      {
                        validator: checkMatrixArray_blur.bind(que),
                        trigger: 'blur',
                      },
                    ]"
                  >
                    <div class="matrix-holder">
                      <div class="header-preview">
                        <div class="empty"></div>
                        <div class="columnTitles">
                          <div
                            v-for="(column, index) in que.matrixMultiple
                              .columnVals"
                            :key="index"
                          >
                            <span>{{ column.content }}</span>
                          </div>
                        </div>
                      </div>
                      <div
                        v-for="(row, index) in que.matrixMultiple.rowVals"
                        :key="index"
                        class="row-item"
                      >
                        <div class="row-item-title">{{ row.content }}</div>
                        <div class="row-item-choices">
                          <el-checkbox-group
                            v-for="column in que.matrixMultiple.columnVals"
                            :key="column.id"
                            v-model="row.value"
                            class="row-item-choice"
                          >
                            <div>
                              <el-checkbox
                                :label="column.content"
                                class="justify"
                              >
                                <br />
                              </el-checkbox>
                            </div>
                          </el-checkbox-group>
                        </div>
                      </div>
                    </div>
                  </el-form-item>
                </el-form>
              </div>
            </div>
            <div id="page">
              <button
                type="button"
                @click="currentPage--"
                :disabled="currentPage === 1"
                :class="[currentPage === 1 ? 'disabled' : 'avaliable']"
              >
                上一页
              </button>
              {{ currentPage }}/{{ totalPage }}页
              <button
                type="button"
                @click="validationCheck"
                :disabled="currentPage === totalPage"
                :class="[currentPage === totalPage ? 'disabled' : 'avaliable']"
              >
                下一页
              </button>
            </div>
          </div>
        </div>
        <div id="generalButtsPreview">
          <div>
            <img
              src="../assets/pictures/settings.svg"
              width="25px"
              height="25px"
            /><span>设置</span>
          </div>
          <div>
            <img
              src="../assets/pictures/save.svg"
              width="23px"
              height="23px"
            /><span>保存</span>
          </div>
          <div>
            <img
              src="../assets/pictures/publish.svg"
              width="21px"
              height="21px"
            /><span>发布</span>
          </div>
          <div @click="toEdit">
            <img
              src="../assets/pictures/back.svg"
              width="18px"
              height="18px"
            /><span>返回</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HeaderComponent from "../components/HeaderComponent.vue";
import { mapState } from "vuex";
import {
  checkStr,
  checkArray,
  isCompleted,
  isZeroRemaining,
  checkMatrixStr_blur,
  checkMatrixStr_change,
  checkMatrixArray_blur,
  checkMatrixArray_change,
} from "../utils/validation.js";
export default {
  name: "Preview",
  components: {
    HeaderComponent,
  },
  data() {
    return {
      onload: false,
      loading: true,
      currentPage: 1,
      ques: [],
      checkStr,
      checkArray,
      isCompleted,
      isZeroRemaining,
      checkMatrixStr_blur,
      checkMatrixStr_change,
      checkMatrixArray_blur,
      checkMatrixArray_change,
    };
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.querySelectorAll("input")[0].focus();
      },
    },
  },
  mounted() {
    this.ques = this.getQues();
    setTimeout(() => {
      this.onload = true;
      this.loading = false;
    }, 500);
  },
  computed: {
    ...mapState(["questionnaireObj"]),
    queName() {
      return this.questionnaireObj.name;
    },
    queIntp() {
      return this.questionnaireObj.interpretation;
    },
    totalPage() {
      return this.questionnaireObj.totalPage;
    },
    quesReceived() {
      return this.questionnaireObj.data;
    },
    quesUltimate() {
      return this.ques.filter((que) => que.type !== "pagingLine");
    },
    quesFiltered() {
      return this.quesUltimate.filter((que) => que.page === this.currentPage);
    },
    whetherShowUp() {
      return function(conditions) {
        for (var i = conditions.length - 1; i >= 0; i--)
          if (conditions[i].itemsVal == false) conditions.splice(i, 1);
        if (conditions == false) return true;
        else {
          let flag = false;
          conditions.map((condition) => {
            var chosenRadio = this.ques[
              this.queUltimateIndex(condition.titleVal)
            ].radio;
            var chosenContent = chosenRadio.value.split("|ST23|")[0];
            var chosenId = findId(chosenRadio.choices, chosenContent);
            if (condition.itemsVal.indexOf(chosenId) != -1) flag = true;
          });
          return flag;
        }
        function findId(arr, value) {
          let uniqueId = 0;
          arr.map((item) => {
            if (item.content === value) uniqueId = item.id;
          });
          return uniqueId;
        }
      };
    },
  },
  methods: {
    getQues() {
      if (this.quesReceived == false) {
        this.$message.warning("无可预览内容！");
        this.$router.push({ name: "HomePage" });
        return [];
      } else {
        let received = JSON.parse(JSON.stringify(this.quesReceived));
        let dispatchedNum = 1;
        received.map((que) => {
          que.page = dispatchedNum;
          if (que.type === "pagingLine") dispatchedNum++;
          if (que.random) {
            if (que.type === "单选题") this.randomize(que.radio.choices);
            else if (que.type === "多选题")
              this.randomize(que.multiple.options);
            else if (que.type === "排序题") this.randomize(que.sort.sortItems);
            else if (que.type === "矩阵单选题")
              this.randomize(que.matrixRadio.columnVals);
            else if (que.type === "矩阵多选题")
              this.randomize(que.matrixMultiple.columnVals);
          }
        });
        return received;
      }
    },
    queUltimateIndex(id) {
      let i = -1;
      this.quesUltimate.map((que, index) => {
        if (que.id === id) i = index;
      });
      return i;
    },
    restrictionTip(que) {
      var tipStr = "";
      var flag = 0;
      if (que.type === "多选题") return result(que.multiple);
      else if (que.type === "矩阵多选题") return result(que.matrixMultiple);
      else return "";
      function result(obj) {
        if (obj.min !== "0") {
          flag = 1;
          if (obj.max !== "不限") flag = 3;
        } else if (obj.max !== "不限") flag = 2;
        switch (flag) {
          case 1:
            tipStr = `[ 至少选${obj.min}项 ]`;
            break;
          case 2:
            tipStr = `[ 最多选${obj.max}项 ]`;
            break;
          case 3:
            tipStr = `[ 请选择${obj.min}-${obj.max}项 ]`;
            break;
          default:
            tipStr = "";
        }
        return tipStr;
      }
    },
    chosen(a, b) {
      if (typeof a === "string") return a.split("|ST23|")[0] === b;
      else {
        let flag;
        a.map((item) => {
          if (item.split("|ST23|")[0] === b) flag = true;
        });
        return flag;
      }
    },
    choiceRemainChosen(choice, radio) {
      if (choice.blank_fill === "") radio.value = choice.content;
      else radio.value = choice.content + "|ST23|" + choice.blank_fill;
    },
    optionRemainChosen(option, multiple) {
      var origin = option.content;
      var i = -1;
      multiple.value.forEach((item, index) => {
        if (item.split("|ST23|")[0] === origin) i = index;
      });
      if (option.blank_fill === "") multiple.value[i] = option.content;
      else multiple.value[i] = option.content + "|ST23|" + option.blank_fill;
    },
    randomize(arr) {
      arr.sort(() => {
        return Math.random() - 0.5;
      });
    },
    sortHandler(sort, sortItem) {
      if (!sort.sortedNum) sortItem.order = ++sort.sortedNum;
      else if (!sortItem.order) sortItem.order = ++sort.sortedNum;
      else if (sortItem.order == sort.sortedNum) {
        sortItem.order = 0;
        sort.sortedNum--;
      } else {
        let tempOrder = sortItem.order;
        sort.sortItems.map((item) => {
          if (item.order > tempOrder) item.order--;
        });
        sortItem.order = 0;
        sort.sortedNum--;
      }
    },
    scrollDefault() {
      this.ques.map((que) => {
        if (que.type === "评分题") que.slider.showToolTip = false;
      });
    },
    validationCheck() {
      let flag = true;
      this.$refs["que"].forEach((item) => {
        item.validate((valid) => {
          if (!valid) {
            console.log("Error submit!");
            flag = false;
            return false;
          }
        });
      });
      if (flag) this.currentPage++;
      else this.$message.warning("请检查您的填写");
    },
    toEdit() {
      this.$router.push({ name: "EditPage" });
    },
  },
};
</script>

<style scoped lang="scss">
#editPreview {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  span {
    word-wrap: break-word;
  }
  #loadingPreview {
    width: 100%;
    height: 100vh;
  }
}
#questionnairePreview {
  width: 100%;
  height: 89vh;
  position: relative;
  left: 0;
  top: 11vh;
  padding: 5vh 70px 6vh;
  overflow: overlay;
  transform: scale(1);
  #topBox {
    height: 4vh;
    font-size: 17px;
    font-weight: 500;
    text-align: left;
    margin: auto;
    padding-left: 5px;
    margin-bottom: 2vh;
    border-bottom: 1.5px solid #bfbfbf;
  }
}
#generalButtsPreview {
  width: 50px;
  position: fixed;
  top: 55.5vh;
  right: calc(10% - 6px);
  transform: translateY(-50%);
  background: #ffffff;
  border-radius: 5px;
  padding: 10px 0;
  div {
    margin: 10px;
    font-size: 10px;
    font-weight: 500;
    color: #515151;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    img {
      margin: 0 auto 5px;
    }
  }
}
@media all and (max-width: 820px) {
  #questionnairePreview {
    width: 820px;
  }
  #editpagePreview {
    transform: scale(1);
  }
  #generalButtsPreview {
    position: fixed;
    top: 55.5vh;
    left: 695px;
  }
  #editPreview {
    overflow-x: auto;
  }
  #editPreview::-webkit-scrollbar {
    display: none;
  }
}
#queBoxPreview {
  min-height: 72vh;
  margin: auto;
  overflow: hidden;
  background: #ffffff;
  border-radius: 7px;
  padding-bottom: 60px;
}
.box {
  width: 80%;
  transform: scale(1);
}
#headPreview {
  border-bottom: 1px solid #bfbfbf;
  #namePreview {
    padding-top: 20px;
    input {
      width: 50%;
      font-size: 18px;
      font-weight: 500;
      color: #2a2a2a;
      margin: auto;
      text-align: center;
      outline: none;
      border: none;
      display: block;
    }
    span {
      font-size: 18px;
      font-weight: 500;
      color: #2a2a2a;
      display: block;
    }
  }
  #interpretationPreview {
    padding: 0px 10px;
    span {
      font-size: 14px;
      font-weight: 500;
      color: #8a8a8a;
      text-align: left;
      display: block;
      margin: 5px auto 5px 5px;
    }
    input {
      width: 90%;
      font-size: 14px;
      font-weight: 500;
      color: #8a8a8a;
      display: block;
      margin: 5px auto 5px 5px;
      text-align: left;
      outline: none;
      border: none;
    }
  }
}
.tip {
  padding: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 14px;
  transform: translate(-50%, -50%);
}
#modelsPreview {
  transform: scale(1);
}
.list-complete-item {
  width: 100%;
  transform: scale(1);
  transition: all 0.25s;
  text-align: left;
  input {
    word-break: break-all;
    font-weight: 500;
    color: #2a2a2a;
  }
  span,
  label {
    word-break: break-all;
    padding: 5px 0;
    display: block;
    font-weight: 500 !important;
  }
  li {
    font-size: 14px;
    list-style-type: none;
    padding: 0px 27px;
    display: flex;
    .choicesInput {
      width: 40%;
      margin: 8px 0 8px 5px;
      font-size: inherit;
      background-color: transparent;
      border-bottom: 2px solid #e2e2e2;
    }
  }
  .redStarHolder {
    height: 100%;
    flex-shrink: 0;
    display: flex;
    img {
      margin-top: 5px;
    }
  }
  .que-title-holder {
    padding: 0 0 5px 15px;
    display: flex;
    transform: scale(1);
    font-size: 14px;
    // align-items: center;
    span {
      padding: 0px !important;
      font-weight: 500;
      margin-left: 5px;
      max-width: calc(100% - 27px);
    }
    input {
      font-size: inherit;
      color: #2a2a2a;
      height: 30px;
      padding: 5px 0 5px 5px;
      margin-right: 5px;
      border: 1px solid #5f7880;
      border-radius: 5px;
      background-color: transparent;
    }
  }
}
.restriction-tip {
  color: #999;
  font-weight: 500;
  font-size: 14px;
  margin-left: 5px;
  display: inline-block;
}
.choices-item,
.options-item,
.sortItems-item {
  text-align: left;
}
.pic-preview {
  margin-left: 42px;
  max-height: 90px;
}

.activeBG {
  background-color: #fbfafa;
}
.choices-item,
.options-item,
.sortItems-item,
.mrcs-item,
.mmcs-item {
  transition: all 0.25s;
}
.choiceIcon {
  display: flex;
  margin: auto 5px;
  img {
    cursor: pointer;
  }
}
.bfa-input {
  margin-left: 17px;
  display: flex;
}
.blank {
  width: 80%;
  border-radius: 5px;
  display: block;
  margin-left: 27px;
  margin-top: 5px;
}
.orderBox {
  position: relative;
  top: 50%;
  transform: translateY(calc(-50% + 1px));
  width: 15px;
  height: 15px;
  text-align: center;
  font-size: 14px;
  line-height: 15px;
  font-weight: 500;
  color: white;
  cursor: pointer;
  background-color: #e2e2e2;
  border-radius: 1px;
  margin-right: 5px;
}
.orderBox-active {
  background-color: #0075ff;
}
.slider {
  margin: 10px 27px 20px;
  width: 50%;
  font-size: 14px;
  transform: scale(0.9) translateX(-5%);
}
.matrix-holder {
  width: 80%;
  margin: 10px 27px;
  .header-editing {
    background-color: #ededed;
    display: flex;
    div {
      height: 30px;
      line-height: 30px;
      font-size: 12px;
      font-weight: 500;
    }
  }
  .header-preview {
    background-color: #ededed;
    display: flex;
    border: 1px solid #e2e2e2;
    .empty {
      flex: 2;
    }
    .columnTitles {
      flex: 3;
      display: flex;
      justify-content: space-around;
      align-items: center;
      text-align: center;
      div {
        flex: 1;
        flex-shrink: 0;
        min-height: 30px;
        span {
          word-break: break-all;
          font-size: 12px;
          font-weight: 500;
          text-align: center;
        }
      }
    }
  }
  .row-item {
    border: 1px solid #e2e2e2;
    border-top: none;
    display: flex;
    min-height: 30px;
    align-items: center;
    .row-item-title {
      flex: 2;
      font-size: 12px;
      font-weight: 500;
      text-align: left;
      margin-left: 10px;
      margin-right: -10px;
      word-break: break-all;
    }
    .row-item-choices {
      flex: 3;
      display: flex;
      .row-item-choice {
        flex: 1;
        div {
          display: flex;
          justify-content: center;
          .justify {
            margin-left: 5px;
          }
        }
      }
    }
  }
  .left-align {
    text-align: left;
    flex: 2;
    padding-left: 10px;
  }
  .inputs-holder {
    display: flex;
    .rowTitles {
      flex: 2;
      .textArea-row {
        width: calc(100% - 5px);
        min-height: 95px;
        margin: 10px 10px 0px 0;
        padding: 5px 10px;
        font-size: 12px;
        font-weight: 500;
        line-height: 20px;
        border: 1px solid #bfbfbf;
        resize: vertical;
      }
    }
    .matrix-choices {
      flex: 3;
      margin-top: 5px;
      li {
        display: flex;
        padding: 0 5px;
        .mrcInput {
          flex: 2;
          height: 25px;
          font-size: 12px;
          font-weight: 500;
          background: #ffffff;
          margin: 5px 0;
          padding: 3px 12px;
          border: 1px solid #bfbfbf;
        }
        .mrcIcon {
          flex: 1;
          display: flex;
          justify-content: center;
          img {
            margin: 0 5px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
.add {
  width: 80px;
  height: 18px;
  box-sizing: content-box;
  margin: 5px auto 5px 25px;
  display: flex;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  color: #8a8a8a;
  border-bottom: 1px solid #8a8a8a;
  cursor: pointer;
}
.edhead {
  text-align: left;
  margin: 15px auto 10px 30px;
  font-size: 14px;
  font-weight: 600;
  color: #515151;
}
.checkBox {
  position: fixed;
  top: 50%;
  left: 75px;
  transform: translateY(-50%);
}
.item {
  margin-top: 10px;
  margin-left: 30px;
  text-align: left;
  font-size: 12px;
  font-weight: 500;
  transform: scale(1);
  display: flex;
  .max-min {
    font-weight: 500;
    margin-left: 30px;
    input {
      width: 50px;
      height: 16px;
      margin: -4px 5px auto;
      text-align: center;
      font-size: 12px;
      border-bottom: 1.5px solid #8a8a8a;
      background-color: transparent;
    }
  }
  .sliderM {
    margin: auto 10px;
    input {
      width: 30px;
    }
  }
}
.jump {
  margin-left: 29px;
  color: #8a8a8a;
  .text {
    border-bottom: 1px solid #8a8a8a;
    margin-right: 50px;
    cursor: pointer;
  }
}
.matrix {
  margin-top: -15px;
}
#page {
  width: 270px;
  height: 60px;
  padding: 10px 0;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 12px;
  font-weight: 500;
  color: #707070;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  button {
    font-size: inherit;
    font-weight: 500;
    color: #707070;
    padding: 3px 12px;
    border: 1px solid #cdcdcd;
    border-radius: 5px;
  }
}
.avaliable {
  background-color: white;
  cursor: pointer;
}
.disabled {
  background-color: #f8f8f8;
  cursor: not-allowed;
}
</style>

