export function checkStr(rule, value, callback) {
  if (value == null || value === "") {
    if (this.compulsory) callback(new Error("此问题必须填写"));
  } else callback();
}
export function checkArray(rule, value, callback) {
  if (value == false) {
    if (this.compulsory) callback(new Error("此问题必须填写"));
  } else {
    let l = value.length;
    if (this.multiple.min !== "0" && l < this.multiple.min) {
      let error = new Error(`此多选题最少选${this.multiple.min}项`);
      callback(error);
    } else if (this.multiple.max !== "不限" && l > this.multiple.max) {
      let error = new Error(`此多选题最多选${this.multiple.max}项`);
      callback(error);
    } else callback();
  }
}
export function isCompleted(rule, value, callback) {
  if (value === 0) {
    if (this.compulsory) callback(new Error("此问题必须填写"));
  } else {
    let total = this.sort.sortItems.length;
    if (value < total) callback(new Error("请将所有项目排序"));
    else callback();
  }
}
export function isZeroRemaining(rule, value, callback) {
  if (value === 0) {
    if (this.compulsory) callback(new Error("此问题必须填写"));
  } else callback();
}
export function checkMatrixStr_change(rule, value, callback) {
  callback();
}
export function checkMatrixStr_blur(rule, value, callback) {
  let emptyRows = [];
  value.forEach((element, index) => {
    if (element.value == null || element.value === "") {
      if (this.compulsory) emptyRows.push(index + 1);
    }
  });
  if (emptyRows == false) callback();
  else {
    if (emptyRows.length === value.length)
      callback(new Error("此问题必须填写"));
    else {
      let str = "";
      emptyRows.forEach((item) => {
        str += `${item}、`;
      });
      str = str.substring(0, str.length - 1);
      callback(new Error(`此题为必答题，请回答小题${str}`));
    }
  }
}
export function checkMatrixArray_change(rule, value, callback) {
  let flag = false;
  value.forEach((element) => {
    let l = element.value.length;
    if (
      l &&
      ((this.matrixMultiple.min !== "0" && l < this.matrixMultiple.min) ||
        (this.matrixMultiple.max !== "不限" && l > this.matrixMultiple.max))
    )
      flag = true;
  });
  if (flag)
    callback(
      new Error(
        `此矩阵多选题每小题最多选${this.matrixMultiple.max}项，
         最少选${this.matrixMultiple.min}项`
      )
    );
  else callback();
}
export function checkMatrixArray_blur(rule, value, callback) {
  let emptyRows = [];
  value.forEach((element, index) => {
    if (element.value == false && this.compulsory) emptyRows.push(index + 1);
  });
  if (emptyRows.length === value.length) callback(new Error("此问题必须填写"));
  else {
    if (emptyRows == false) callback();
    else {
      let str = "";
      emptyRows.forEach((item) => {
        str += `${item}、`;
      });
      str = str.substring(0, str.length - 1);
      callback(new Error(`此题为必答题，请回答小题${str}`));
    }
  }
}
